import {SESSION, LOGOUT} from '../models/types';

const initialState = {
    auth: JSON.parse(localStorage.getItem(SESSION) || '{}')
}

export default function(state = initialState, action: any) {
    switch (action.type) {
        case SESSION:
        case LOGOUT:
          return {
            ...state,
            auth: action.payload,
          }
        default:
            return state;
        }
}
