import {REGISTER, SESSION} from '../models/types';
import axios from "axios";
import store from "../store";
import {BASE_URL, mockSuccessRegistration} from './../../environment'

import { setSession } from './session';
import {logError, logServerRequestTime, setUser} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";

export async function register(data: any){
    const errors: any = {};

    //in order to not write to database, specially now that we can't delete users yet
    if(mockSuccessRegistration) {
        console.log('IS mockSuccessRegistration')
        store.dispatch({
            type: REGISTER,
            payload: 'TEST'
        });
        setSession(SESSION, 't1h2i3s4i5s6a7t8e9s0t9t8o7k65e4n3f2o1r2e3n4d5t6o7e8nd9t0e9s8t7i6n5g4');
        return {
            errors,
            isRegistered: true
        }
    }

    try{
        const sentTime = (new Date()).getTime();
        let response = await axios.post(BASE_URL + '/register/', {
            "email": data.email,
            "username": data.username,
            "password": data.password
        })
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.REGISTER, receivedTime-sentTime);

        setSession(SESSION, response.data);
        setUser(data);

        store.dispatch({
            type: REGISTER,
            payload: response.data.user.username
        });
        return {
            errors,
            isRegistered: true
        }
    }
    catch(e){
        if(e.response.data){
            console.log(e.response.data)
            if(e.response.data.email){
                errors.email = e.response.data.email[0];
            }
            if(e.response.data.username){
                errors.username = e.response.data.username[0];
            }
            logError(RequestName.REGISTER, e.response.data, STACK.SERVER)
        }
        return {
            errors,
            isRegistered: false
        };
    }

}
