import {GUEST, LOGIN, REGISTER, EDIT, SESSION} from '../models/types';

// Get the username from the session
const initialState = {
    username: localStorage.getItem(SESSION) ? JSON.parse(localStorage.getItem(SESSION) || '{}')?.user?.username : ''
}

export default function(state = initialState, action: any) {
    switch (action.type) {
        case LOGIN:
        case REGISTER:
        case GUEST:
        case EDIT:
            return {
                ...state,
                username: action.payload,
            }
        default:
            return state;
    }
}
