import React, { Component } from 'react';
import {CardDeck} from 'react-bootstrap';
import "./Resources.css";
import store from "../../../../state/store";
import {getResources} from "../../../../state/actions/resources";
import CardContainer from "../../../../common/components/cards/Cards";
import {logUserAction} from "../../../../state/actions/analytics";
import {ButtonClicks} from "../../../../state/models/analyticsModels";

export interface Props {
    pathname?: string;
}
interface State {
    username: string,
    resourcesList: any[]
}
class Resources extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            resourcesList: []
        }
    }

    async componentDidMount() {
        await getResources();
        const resourcesList = store.getState().resources.list;
        this.setState({resourcesList});
    }

    goToWebsite(url: string){
        logUserAction(ButtonClicks.RESOURCE)
        window.open(url);
    }


    render() {
        return (
            <>
            <h3 className="header-dashboard">Help</h3>
                <div className="resources-page-wrapper">
                    {this.state.resourcesList.map(
                        (resource) => {
                            return <div key={resource.title} className='resources-card'>
                                <CardDeck onClick={() => this.goToWebsite(resource.link)}>
                                    <CardContainer
                                        name={resource.title}
                                        description={resource.description}
                                        hideProgress={true}
                                        imageUrl={resource.picture}
                                    >
                                    </CardContainer>
                                </CardDeck>
                            </div>
                        }
                    )}
                </div>
            </>
        )
    }
}


export default Resources;
