import React from 'react';
import './App.css';
import {Provider} from 'react-redux';
import store from './state/store';
import Homepage from './pages/homepage/Homepage';
import {Switch} from "react-router-dom";
import Dashboard from "./pages/dashboard/Dashboard";
import { PrivateRoute, AuthRoute }  from './common/components/PrivateRoute';
import {logPageNavigated, setUser} from "./state/actions/analytics";
import {PageNavigation} from "./state/models/analyticsModels";
import IndividualQuiz from './pages/dashboard/components/quizzes/individual-quiz/IndividualQuiz';

export interface AppProps {
    username: string
}
export enum Paths{
  root = "/",
  dashboard = "/dashboard",
  modules = "/dashboard/modules",
  lessons = "/dashboard/lessons",
  lessons_moduleId = "/dashboard/lessons/:moduleId",
  quizzesPrefix = "/dashboard/quizzes",
  quizzesModules = "/dashboard/quizzes/modules",
  quizzes = "/dashboard/quizzes/:moduleId",
  quiz = "/dashboard/quizzes/:moduleId/quiz/:quizId",
  games = "/dashboard/games",
  editProfile = "/dashboard/editProfile",
  help = "/dashboard/help",
  individualLesson = "/dashboard/lessons/:moduleId/:lessonId"
}

function App() {
    if (window.Cypress) {
        window.store = store
    }

    if(store.getState().session?.auth?.user) {
        setUser(store.getState().session.auth.user);
        logPageNavigated(PageNavigation.INIT)
    }

  return (

    <Provider store={store}>

      <div className="App">
          <Switch>
              <AuthRoute path={Paths.root} component={Homepage} exact />
              <PrivateRoute exact path={Paths.quiz} component={IndividualQuiz} />
              <PrivateRoute path={Paths.dashboard} component={Dashboard} />
              


              {/*TODO>>>>>>>>>*/}
              {/*<Route path="/about" component={About} />*/}
              {/*<Route path="/contact" component={Contact} />*/}
              {/*<Route path="/career" component={Careers} />*/}
              {/*<Route path="/terms" component={Terms} />*/}
              {/*<Route path="/privacy" component={Privacy} />*/}
          </Switch>
      </div>

    </Provider>
  );
}

export default App;
