import React, { Component } from 'react';
import { Col, Row } from 'react-bootstrap';
import { connect } from 'react-redux';
import DashboardNav from './components/DashboardNav';
import SideBar from './components/Sidebar';
import store from "../../state/store";
import Modules from "./components/modules/Modules";
import Lesson from "./components/lessons/Lesson";
import EditProfile from "./components/edit-profile/EditProfile";
import Resources from "./components/resources/Resources";
import {Paths} from "../../App";
import IndividualLesson from "./components/IndividualLesson/IndividualLesson";
import Header from "../../common/components/header/Header";
import {logPageNavigated} from "../../state/actions/analytics";
import {PageNavigation} from "../../state/models/analyticsModels";
import Quizzes from "./components/quizzes/Quizzes";

export interface Props {
    location?: {
        pathname?: string,
        match?: any
    }
}
interface State {
    username: string,
    moduleId?: string,
    lessonId?: string,
    quizId?: string
}
class Homepage extends Component<Props, State> {
    constructor(props: Props) {
        super(props);
        this.state = {
            username: store.getState().user.username
        }
        logPageNavigated(PageNavigation.DASHBOARD)
    }


    componentDidUpdate(prevProps: Readonly<Props>) {
        if (prevProps?.location?.pathname !== this.props.location?.pathname) {
            this.updateAndNotify();
        }
    }

    updateAndNotify = () => {
        let lessonId = undefined;
        let moduleId = undefined;
        let quizId = undefined;
        this.setState({moduleId, lessonId, quizId});

        if (this.props.location?.pathname?.includes('quizzes') && !this.props.location?.pathname?.includes('modules')) {
            moduleId = this.props.location?.pathname.substring(this.props.location.pathname.indexOf('quizzes/')+8);

        } else if (this.props.location?.pathname?.includes('quiz/')) {
            quizId = this.props.location?.pathname.substring(this.props.location.pathname.indexOf('quiz/')+5);

        } else if(this.props.location?.pathname?.includes('lessons/')) {
            moduleId = this.props.location?.pathname.substring(this.props.location.pathname.indexOf('lessons/')+8);
            if(moduleId.includes('/')) {
                lessonId = moduleId.substring(moduleId.indexOf('/')+1);
                moduleId = moduleId.substring(0, moduleId.indexOf('/'));
            }
        }
        this.setState({moduleId, lessonId, quizId});
    }


    render() {
        return (
            <>
                <Header />
                <Row>
                    <Col lg={2}>
                        {!this.state.lessonId
                            ? <SideBar location={this.props.location} username={this.state.username} />
                            : null
                        }

                    </Col>
                    <hr className="title-hr"/>
                    <Col>
                        {this.props.location?.pathname === Paths.dashboard
                            ? <DashboardNav />
                            : null
                        }
                        {this.props.location?.pathname === Paths.modules || this.props.location?.pathname === Paths.quizzesModules
                            ? <Modules pathname={this.props.location?.pathname} />
                            : null
                        }
                        {this.state.moduleId && this.state.lessonId
                            ? <IndividualLesson lessonId={this.state.lessonId} moduleId={this.state.moduleId} />
                            : this.state.moduleId
                                ? (this.props.location?.pathname?.includes('quizzes')
                                    ? <Quizzes pathname={this.props.location?.pathname} moduleId={this.state.moduleId} />
                                    : <Lesson pathname={this.props.location?.pathname} moduleId={this.state.moduleId} />)
                                : null
                        }
                        {this.props.location?.pathname === Paths.editProfile
                            ? <EditProfile pathname={this.props.location?.pathname} />
                            : null
                        }
                        {this.props.location?.pathname === Paths.help
                            ? <Resources pathname={this.props.location?.pathname} />
                            : null
                        }

                    </Col>
                </Row>
            </>
        )
    }
}
const mapStateToProps = (state: any) => ({
    username: state.user.username
})
export default connect(mapStateToProps, {})(Homepage);
