import React from "react";
import { connect } from "react-redux";
import "./Quizzes.css";
import store from "../../../../state/store";
import { logPageNavigated } from "../../../../state/actions/analytics";
import { PageNavigation } from "../../../../state/models/analyticsModels";
import { getQuizzes } from "../../../../state/actions/quizzes";
import { NavLink } from "react-router-dom";
import { Card, CardColumns } from "react-bootstrap";
import CardContainer from "../../../../common/components/cards/Cards";
import { Paths } from "../../../../App";
import {getModules} from "../../../../state/actions/modules";

export interface QuizzesProps {
    modules: any[][];
    getModules: () => void;

    pathname?: string;
    moduleId: string;
}

export interface QuizzesState {
    username: string;
    quizzes: any;

}

class Quizzes extends React.Component<QuizzesProps, QuizzesState>{
    constructor(props: QuizzesProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            quizzes: null
        }
        logPageNavigated(PageNavigation.QUIZZES);
    }

    async componentDidMount() {
        const quizzes = await getQuizzes(this.props.moduleId);
        this.setState({ quizzes })

        await getModules();
    }

    getModuleName(){
        const module = this.props.modules.filter((moduleAsArray: any[]) => {
            const moduleID = moduleAsArray[0];
            return moduleID === parseInt(this.props.moduleId, 10)
        })[0]

        if(module)
        {
            return module[1]
        }
    }

    render() {
        return (
            <>
                <h3 className='header-dashboard'>{this.getModuleName()}</h3>
                <div className="quizzes-wrapper">
                    <CardColumns>
                        {this.state.quizzes
                            ? this.state.quizzes.map(
                                (quiz: any) => {
                                    const to: string = Paths.quiz.replace(':moduleId', this.props.moduleId).replace(':quizId', quiz.quiz_id);
                                    return <Card
                                        style={{ width: '70%', marginTop: '10%' }}
                                        key={quiz.quiz_id}>
                                        <NavLink key={quiz.quiz_id} to={to} className="no-margin">
                                            <CardContainer
                                                name={quiz.title}
                                                description={quiz.description}
                                                progress={0}
                                                imageUrl={quiz.resources} />

                                        </NavLink>
                                    </Card>
                                }
                            )
                            : null}
                    </CardColumns>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state: any) => ({
    quizzes: state.module.quizzes,
    modules: state.module.modules
})
const mapDispatchToProps = {
    getModules: getModules
}

export default connect(mapStateToProps, mapDispatchToProps)(Quizzes);
