import {GET_LESSON, GET_MODULE_LESSONS, GET_MODULES, SESSION, SET_LESSON_NAME} from '../models/types';
import axios from "axios";
import { BASE_URL } from '../../environment'
import store from "../store";
import {logError, logServerRequestTime} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";


export async function getModules() {
    try {
        let username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username
        const sentTime = (new Date()).getTime();
        let response = await  axios.get(BASE_URL + '/api/modules' + (username !== 'guest' ? `?username=${username}` : ''))
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.MODULES, receivedTime-sentTime);
        store.dispatch({
            type: GET_MODULES,
            payload: response.data?.response_text
        })
    } catch(e){
        logError(RequestName.MODULES, e, STACK.SERVER)
        console.error("Failed to get modules", e);
    }
}

export async function getModuleLessons(id : string) {
    try{
        let username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username
        const sentTime = (new Date()).getTime();
        let response = await axios.get(BASE_URL + '/api/modules/' +id+"/lesson"+
            (username !== 'guest' ? `?username=${username}` : ''));
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.MODULE_LESSONS, receivedTime-sentTime);
        store.dispatch({
            type: GET_MODULE_LESSONS,
            payload: response.data["lessons"]
        });
        return true;
    }
    catch(e){
        logError(RequestName.MODULE_LESSONS, e, STACK.SERVER)
        return false;
    }
}

export async function getLessonContent(lessonid:string) {
    try{
        const sentTime = (new Date()).getTime();
        let response = await axios.get(BASE_URL + '/api/modules/lesson/' +lessonid);
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.LESSON_CONTENT, receivedTime-sentTime);
        
        console.log("response" , response);
        store.dispatch({
            type: GET_LESSON,
            payload: response.data["slide"]
        });
        return true;
    }
    catch(e){
        logError(RequestName.LESSON_CONTENT, e, STACK.SERVER)
        return false;
    }
}
export async function updateLessonProgress(lessonId:string, progress:number){
    try{
        let username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username
        const sentTime = (new Date()).getTime();
        await axios.get(BASE_URL + '/api/modules/lesson/'+lessonId+"/progress/"+progress+
            (username !== 'guest' ? `?username=${username}` : ''));
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.UPDATE_PROGRESS, receivedTime-sentTime);
    }
    catch(e){
        logError(RequestName.UPDATE_PROGRESS, e, STACK.SERVER)
        console.log(e);
    }
}
export function setCurrentLesson(id: string) {
    try {
        store.dispatch({
            type: SET_LESSON_NAME,
            payload: id
        })
    } catch(e) {
        console.log(e);
    }
}
