import React, { SyntheticEvent } from 'react'
import Form from 'react-bootstrap/Form'
interface CredentialsProps {
    onChangeUsername: (event: SyntheticEvent) => void;
    onChangePassword: (event: SyntheticEvent) => void;
    errors: any;
}

class Credentials extends React.Component<CredentialsProps>{
    render() {
        return (
            <>
                <Form.Group
                    controlId="formBasicUsername">

                    <Form.Control
                        type="text"
                        data-cy='username-input'
                        placeholder="Username"
                        onChange={this.props.onChangeUsername}
                    />

                    {this.props.errors.username ? (
                        <p data-cy='username-error' className='invalid-input'>{this.props.errors.username}</p>
                    ) : null}

                </Form.Group>
                <Form.Group
                    controlId="formBasicPassword">

                    <Form.Control
                        type="password"
                        data-cy='password-input'
                        placeholder="Password"
                        onChange={this.props.onChangePassword}
                    />

                    {this.props.errors.password ? (
                        <p data-cy='password-error' className='invalid-input'>{this.props.errors.password}</p>
                    ) : null}
                </Form.Group>
            </>
        )
    }
}
export default Credentials;
