import {logError} from "../actions/analytics";
import {FunctionName, STACK} from "../models/analyticsModels";

const validator = require('validator');
const isEmpty = require('lodash/isEmpty');

export default function validateRegistrationInput(data: any) {
    let errors: any = {};

    if(validator.isEmpty(data.username)) {
        errors.username = 'This field is required'
    } else if(data.username.length < 4 || data.username.length > 100) {
        errors.username = 'Invalid username, username needs to be between 4 and 100 characters';
    }
    if(validator.isEmpty(data.email)) {
        errors.email = 'This field is required'
    } else if(data.email.length < 4 || data.email.length > 100 || !data.email.includes('@')) {
        errors.email = 'Invalid email address';
    }

    if(validator.isEmpty(data.password)) {
        errors.password = 'This field is required'
    } else if(data.password.length < 4 || data.password.length > 100 ) {
        errors.password = 'Invalid password, must be between 4 and 100 characters';
    }

    if(!isEmpty(errors)) {
        logError(FunctionName.REGISTER_INPUT, errors, STACK.CLIENT)
    }
    return {
        errors,
        isValid: isEmpty(errors)
    }
}
