import {LOGIN, SESSION} from '../models/types';
import axios from "axios";
import store from "../store";
import {BASE_URL} from '../../environment'

import {setSession} from './session';
import {logError, logServerRequestTime, setUser} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";

export async function login(data: any) {
    try{
        const sentTime = (new Date()).getTime();
        let response = await axios.post(BASE_URL + '/user/auth', {
            "username": data.username,
            "password": data.password
        })
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.LOGIN, receivedTime-sentTime);

        setSession(SESSION, response.data);
        setUser(data);

        store.dispatch({
            type: LOGIN,
            payload: response.data.user.username
        });
        return true;
    }
    catch(e){
        logError(RequestName.LOGIN, e.response.data, STACK.SERVER)
        return false;
    }
}
