import {
    GET_LESSON,
    GET_MODULE_LESSONS,
    GET_MODULES,
    GET_QUIZ_DATA,
    GET_QUIZZES,
    SET_LESSON_NAME
} from "../models/types";

interface ModuleState {
    modules: string[][];
    lessons: any[][];
    currentLesson: any;
    currentLessonName: string;
    quizzes: any[];
    quizzes_data: any;
}
const initialState: ModuleState = {
    modules: [],
    lessons: [],
    currentLesson: {},
    currentLessonName: "",
    quizzes: [],
    quizzes_data: []
}

export default function (state = initialState, action: any) {
    switch (action.type) {
        case GET_MODULES:
            return {
                ...state,
                modules: action.payload
            }

        case GET_MODULE_LESSONS:
            return {
                ...state,
                lessons: action.payload
            }

        case GET_LESSON:
            return {
                ...state,
                currentLesson: action.payload
            }
        case SET_LESSON_NAME:
            return   {
                ...state,
                currentLessonName: action.payload
            }
        case GET_QUIZZES:
            return {
                ...state,
                quizzes: action.payload
            }
        case GET_QUIZ_DATA:
            return {
                ...state,
                quizzes_data: action.payload
            }
        default:
            return state;
    }
}
