import React from 'react'
import Button from 'react-bootstrap/Button'
import 'bootstrap/dist/css/bootstrap.min.css';
interface SignInOptionsProps {
    id?: string;
    onLoginClicked: (event: React.MouseEvent) => void;
    onContinueWithGoogleClicked: (event: React.MouseEvent) => void;
}
class SignInOptions extends React.Component<SignInOptionsProps>{
    render() {
        return (
            <div id={this.props.id}>
                <Button data-cy='submit-login' id="modalSignInButton" variant="primary" type="submit" onClick={this.props.onLoginClicked}>
                    Login
                    </Button>
                <p>Forgot your password?  <a href="/">click here</a>.</p>
                <p className="horizontalLine"><span>or</span></p>
                <Button id="modalContinueWithGoogleButton" variant="primary" type="submit" onClick={this.props.onContinueWithGoogleClicked}>
                    Continue with Google
                    </Button>
            </div>
        )
    }
}
export default SignInOptions;
