import React, {SyntheticEvent} from "react";
import {Form, Modal} from "react-bootstrap";
import Credentials from "./credentials/Credentials";
import 'bootstrap/dist/css/bootstrap.min.css';
import './UserManagement.css'
import SignInOptions from "./sign-in/SignInOptions";
import SignUpOptions from "./sign-up/SignUpOptions";
import validateLoginInput from "../../../state/validations/login";
import {login} from '../../../state/actions/login';
import {register} from '../../../state/actions/register';
import {connect} from "react-redux";
import validateRegistrationInput from "../../../state/validations/register";
import {TypeOfUserManagement} from "../../../state/models/userManagementModel";
import {logPageNavigated, logUserAction} from "../../../state/actions/analytics";
import {ButtonClicks, PageNavigation} from "../../../state/models/analyticsModels";

interface UserManagementProps {
    type: TypeOfUserManagement
    visible?: boolean;
    onHide?: (isHidden: boolean) => void;
    login: any,
    username: string
}

interface UserManagementState {
    username: string,
    email: string,
    password: string,
    errors: any
}

class UserManagement extends React.Component<UserManagementProps, UserManagementState>{
    constructor(props: UserManagementProps) {
        super(props);
        this.state = {
            username: '',
            email: '',
            password: '',
            errors: '',
        }

        if(this.props.type === TypeOfUserManagement.LOGIN) {
            logPageNavigated(PageNavigation.LOGIN);
        } else if(this.props.type === TypeOfUserManagement.SIGN_UP) {
            logPageNavigated(PageNavigation.REGISTER);
        }
    }

    handleChangeUsername = (event: SyntheticEvent) => {
        const username = (event.target as HTMLInputElement).value;
        this.setState({username});
    };

    handleChangeEmail = (event: SyntheticEvent) => {
        const email = (event.target as HTMLInputElement).value;
        this.setState({email});
    };

    handleChangePassword = (event: SyntheticEvent) => {
        const password = (event.target as HTMLInputElement).value;
        this.setState({password});
    };

    handleSignUpClicked = async (e: React.MouseEvent) => {
        e.preventDefault();
        logUserAction(ButtonClicks.REGISTER);
        if(this.isRegistrationValid()){
            const data = {
                username: this.state.username,
                password: this.state.password,
                email: this.state.email
            }
            const response:any = await register(data);
            if(response && !response.isRegistered){
                this.setState({errors:response.errors});
            }
        }
    }

    handleContinueWithGoogleClicked = (e: React.MouseEvent) => {
    }
    handleHideClicked = (e: React.MouseEvent) => {
        let errors = "";
        this.setState({errors});
        if (this.props.onHide) this.props.onHide(false);
    }

    handleLoginClicked = async (e: React.MouseEvent) => {
        e.preventDefault();
        logUserAction(ButtonClicks.REGISTER);

        if(this.isLoginValid()) {
            const data = {
                username: this.state.username,
                password: this.state.password
            }

            const res:any = await login(data);
            if(!res) {
                let errors = {
                    notFound: 'Login failed: invalid username or password'
                }
                this.setState({errors});
            }
        }
    }

    handleNavigateToLoginClicked = (e: React.MouseEvent) => {
    }


    isLoginValid() {
        const {errors, isValid} = validateLoginInput(this.state);
        if(!isValid) {
            this.setState({errors});
        }
        return isValid;
    }

    isRegistrationValid(){
        const{errors, isValid} = validateRegistrationInput(this.state);
        if(!isValid){
            this.setState({errors});
        }
        return isValid;
    }

    render() {
        return <Modal
            show={this.props.visible}
            onHide={this.handleHideClicked}
            backdrop="static"
            keyboard={false}>

            <Modal.Header closeButton>
                <Modal.Title>
                    {this.props.type}
                </Modal.Title>
            </Modal.Header>

            <Form className="form">
                <Modal.Body>
                    {this.props.type === TypeOfUserManagement.SIGN_UP ? (<>
                        <Form.Group controlId="formBasicEmail">
                            <Form.Control
                                data-cy='email-input'
                                type="email"
                                placeholder="Email"
                                onChange={this.handleChangeEmail}
                            />
                        {this.state.errors.email ? (
                        <p data-cy='email-error' className='invalid-input'>{this.state.errors.email}</p>
                    ) : null}

                        </Form.Group>
                    </>) : null}
                    <Credentials
                        onChangeUsername={this.handleChangeUsername}
                        onChangePassword={this.handleChangePassword}
                        errors={this.state.errors}
                    />
                    {this.state.errors.notFound ? (
                        <p data-cy='wrong-credentials-error' className='invalid-input'>{this.state.errors.notFound}</p>
                    ) : null}
                </Modal.Body>

                <Modal.Footer>
                    {this.props.type === TypeOfUserManagement.LOGIN ?
                        <SignInOptions
                            id="userManagementSignInButtons"
                            onContinueWithGoogleClicked={this.handleContinueWithGoogleClicked}
                            onLoginClicked={this.handleLoginClicked}
                        /> : null}

                    {this.props.type === TypeOfUserManagement.SIGN_UP ?
                        <SignUpOptions
                            id="userManagementSignUpButtons"
                            onSignUpClicked={this.handleSignUpClicked}
                            onContinueWithGoogleClicked={this.handleContinueWithGoogleClicked}
                            onLoginClicked={this.handleNavigateToLoginClicked}
                        /> : null}
                </Modal.Footer>
            </Form>

        </Modal>
    }
}

const mapStateToProps = (state: any) => ({
    username: state.username
})

export default connect(mapStateToProps, { login })(UserManagement);
