import React from "react";
import { NavLink } from "react-router-dom";
import Sidebar from "react-sidebar";
import './Sidebar.css'
import { MdAccessTime, MdDashboard, MdEdit, MdHelp, MdLibraryBooks, MdVideogameAsset } from 'react-icons/md';
import Avatar from 'react-avatar';
import { Paths } from "../../../App";
import store from "../../../state/store";
import {ButtonClicks} from "../../../state/models/analyticsModels";
import {logUserAction} from "../../../state/actions/analytics";

interface SidebarProps {
  username?: string;
  location?: { pathname?: string };
}
interface SidebarState {
  docked?: boolean;
}
class SideBar extends React.Component<SidebarProps, SidebarState> {
  state: SidebarState = {
    docked: true
  }

  render() {
    return (
      <>
        <Sidebar shadow={false} transitions={false}
          sidebar={
            <>
              <div className="avatarContainer">
                <Avatar
                  name={store.getState().user.username}
                  round={true}
                  color="#FFFFFF"
                  style={{ border: 'solid 10px #6F488E', width: '60', height: '60' }}
                  size="60"
                  fgColor="#5C5C5C"
                />
                {store.getState().user.username}
              </div>
              <ul>
                <li
                    className={this.props.location?.pathname === Paths.dashboard ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.DASHBOARD)}}>
                  <MdDashboard />
                  <NavLink to={Paths.dashboard} data-cy='main-dashboard' activeClassName="non-exact-match">Dashboard</NavLink>
                </li>
                <li
                    className={this.props.location?.pathname?.includes(Paths.modules) || this.props.location?.pathname?.includes(Paths.lessons) ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.MODULES)}}>
                  <MdLibraryBooks />
                  <NavLink to={Paths.modules} data-cy='modules-dashboard'>Modules</NavLink>
                </li>
                <li
                    className={this.props.location?.pathname?.includes(Paths.quizzesPrefix) ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.QUIZZES)}}>
                  <MdAccessTime />
                  <NavLink to={Paths.quizzesModules} data-cy='quizzes-dashboard'>Quizzes</NavLink>
                </li>
                <li
                    className={this.props.location?.pathname === Paths.games ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.GAMES)}}>
                  <MdVideogameAsset />
                  <NavLink to={Paths.games} data-cy='games-dashboard'>Games</NavLink>
                </li>
                <li
                    className={this.props.location?.pathname === Paths.editProfile ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.EDIT_PROFILE)}}>
                  <MdEdit />
                  <NavLink to={Paths.editProfile} data-cy='edit-dashboard'>Edit Profile</NavLink>
                </li>
                <li
                    className={this.props.location?.pathname === Paths.help ? "active" : ""}
                    onClick={() => {logUserAction(ButtonClicks.RESOURCE)}}>
                  <MdHelp />
                  <NavLink to={Paths.help} data-cy='help-dashboard'>Help</NavLink>
                </li>
              </ul>
            </>
          }
          docked={this.state.docked}
          styles={{
            root: {
              height: "100%"
            },
            sidebar: {
              overflowY: 'none',
              background: "#F1F1F1",
              height: "100%"
            },
            overlay: { backgroundColor: 'white' }
          }}
        >
          <></>
        </Sidebar>
      </>
    );
  }
}
export default SideBar;
