import React, {Component} from "react";
import {connect} from 'react-redux';
import logo from '../../../assets/logo.png';
import {Link} from "react-router-dom";
import SignInButton from "../user/buttons/SignInButton";
import './Header.css'
import {Paths} from "../../../App";
import {Nav, Navbar} from "react-bootstrap";
import {MdExitToApp} from "react-icons/md";
import {clearSession} from "../../../state/actions/session";
import {logUserAction} from "../../../state/actions/analytics";
import {ButtonClicks} from "../../../state/models/analyticsModels";


export interface Props {
    postData?: any,
}

class Header extends Component<Props> {
    constructor(props: Props) {
        super(props);
        this.DashboardHeader = this.DashboardHeader.bind(this);
    }

    handleLogout = async (e: React.MouseEvent) => {
        logUserAction(ButtonClicks.LOGOUT)
        clearSession();
    }

    HomepageHeader() {
        return (
            <div data-testid="header" className="app-header">
                <div className="left-header-icon">
                    <div className='header-logo-wrapper'>
                        <img src={logo} className='header-logo' alt='in plain sight logo'/>
                    </div>
                    <div className="header-title">
                        <span>In Plain Sight</span>
                    </div>
                </div>

                <div className="right-header-buttons">
                    <div className="header-button header-material-button">
                        <SignInButton />
                    </div>
                    <div className="header-button">
                        <Link id='resources' to="/resources"><span className='white-txt'>Resources</span></Link>
                    </div>
                    <div className="header-button">
                        <Link id='home' to="/"><span className='white-txt'>Home</span></Link>
                    </div>
                </div>
            </div>
        );
    }

    DashboardHeader() {
        return (
            <div>
                <Navbar expand="lg" bg="dark" variant="dark">
                    <Navbar.Brand>
                        <div className='header-logo-wrapper'>
                            <img src={logo} className='header-logo' alt='in plain sight logo' />
                        </div>
                    </Navbar.Brand>
                    <Nav>
                        <Nav.Link data-cy='logout' href="/" onClick={this.handleLogout}><MdExitToApp size={35} /></Nav.Link>
                    </Nav>
                </Navbar>
            </div>
        )
    }

    render() {
        return (
            <>
                { window.location.pathname === '/'
                    ? <this.HomepageHeader/>
                    : window.location.pathname.includes(Paths.dashboard)
                        ? <this.DashboardHeader/>
                        : null
                }
            </>
        )
    }
}

const mapStateToProps = (state: any) => ({
})

export default connect(mapStateToProps, {})(Header);


