import React from "react";
import { Container, Col, Row } from "react-bootstrap";
import "./IndividualLesson.css";
import store from "../../../../state/store";
import { Paths } from "../../../../App";
import { NavLink } from "react-router-dom";
import {getLessonContent, updateLessonProgress} from "../../../../state/actions/modules";
import {logPageNavigated, logUserAction} from "../../../../state/actions/analytics";
import {ButtonClicks, PageNavigation} from "../../../../state/models/analyticsModels";

export interface LessonProps {
    pathname?: string;
    moduleId?: string;
    lessonId?: string;
}
export interface LessonState{
    username:string,
    lessonIndex: number,
    lessonData: any,
    lessonId:string,
    lessonName:string,
    moduleId: string;
}
class IndividualLesson extends React.Component<LessonProps, LessonState>{
    constructor(props: LessonProps){
        super(props);
        this.state = {
            username: store.getState().user.username,
            lessonName: store.getState().module.currentLessonName,
            lessonIndex: 0,
            lessonData: [],
            lessonId:this.props.lessonId || '',
            moduleId: this.props.moduleId || '',
        }
        logPageNavigated(PageNavigation.LESSON);
        this.incrementLessonIndex = this.incrementLessonIndex.bind(this);
        this.decrementLessonIndex = this.decrementLessonIndex.bind(this);
    }

    async componentDidMount() {
        await getLessonContent(this.state.lessonId);

        const lessonData = store.getState().module.currentLesson;
        if(lessonData.length !== 0) {
            this.setState({lessonData});
        }

    }
    incrementLessonIndex(){
        let newIndex = this.state.lessonIndex + 1;
        logUserAction(ButtonClicks.NEXT_SLIDE)
        this.setState({
            lessonIndex:newIndex
        });
        
        let progress = Math.ceil(newIndex*100/this.state.lessonData.length);
        updateLessonProgress(this.state.lessonId, progress);

    }
    decrementLessonIndex(){
        let newIndex = this.state.lessonIndex - 1;
        logUserAction(ButtonClicks.PREVIOUS_SLIDE)
        this.setState({
            lessonIndex:newIndex
        });

        let progress = Math.ceil(newIndex*100/this.state.lessonData.length);
        updateLessonProgress(this.state.lessonId, progress);
    }
    finishLesson(){
        let progress = 100;
        updateLessonProgress(this.state.lessonId, progress);
    }
    render() {
        let previousButton;
        let nextButton;
        let lessonImage;
        let lessonTitle;
        //show previous button if not on first lesson (has no previous lesson)
        if(this.state.lessonIndex !== 0){
            previousButton =  <button onClick={this.decrementLessonIndex}>Previous</button>;
        }
        //if at the last lesson, show finish button that returns to lessons page
        if(this.state.lessonIndex === this.state.lessonData.length-1){
            this.finishLesson();
            nextButton = <li onClick={() => {logUserAction(ButtonClicks.FINISH_LESSON)}}><NavLink className="nav-link" to={Paths.lessons + "/" + this.state.moduleId} data-cy='main-dashboard'><button>Finish</button></NavLink></li>;
        }
        //if not at the last lesson, show the next button
        else{
            nextButton =  <button onClick={this.incrementLessonIndex}>Next</button>;
        }
        //get lesson content
        if(this.state.lessonData && this.state.lessonData.length !== 0){
            lessonTitle = <h3 className="lesson-title">{this.state.lessonData[this.state.lessonIndex].title}</h3>
            lessonImage = <img className="lesson-img" src={this.state.lessonData[this.state.lessonIndex].content} alt={this.state.lessonName}/>
        }

        return (
            <>
                <Container>
                    <div className="lesson-header">
                        <Row>
                            <Col>
                            <h3>{this.state.lessonName}</h3>
                            </Col>
                            <Col/>
                            <Col>
                                <li onClick={() => {logUserAction(ButtonClicks.CLOSE_LESSON)}}>
                                    <NavLink key={this.state.moduleId} to={Paths.lessons + "/" + this.state.moduleId}>
                                        <button className="exit-button">X</button>
                                    </NavLink>
                                </li>
                            </Col>
                        </Row>
                    </div>

                </Container>
                
                <div>
                    {lessonTitle}
                    {lessonImage}
                </div>
                <Container>
                    <div className="nav-buttons">
                        <Row>
                            <Col>
                               {previousButton}
                            </Col>
                            <Col/>
                            <Col>
                                {nextButton}
                            </Col>
                        </Row>

                    </div>

                </Container>

            </>
        )
    }
}

export default IndividualLesson
