import React from "react";
import { Col, Container, Row, Image} from "react-bootstrap";
import store from "../../../../../state/store";
import "./MultipleChoice.css";
import { QuizQuestion } from "../types/Quiz";

export interface MultipleChoiceQProps {
    question: QuizQuestion;
    onQuestionAnswered: (e: React.MouseEvent) => void;
}

export interface MultipleChoiceQState {
    username: string;
    indexSelected?: number;
}

class MultipleChoice extends React.Component<MultipleChoiceQProps, MultipleChoiceQState>{
    constructor(props: MultipleChoiceQProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            indexSelected: undefined
        }
    }
    handleImageClick= (e: React.MouseEvent, index: number) =>{
        this.setState({indexSelected: index});
        this.props.onQuestionAnswered(e);
    }
    isAnswerSelected = () => this.state.indexSelected !== undefined;
    isRightAnswer = (index: number) => this.props.question.answer.includes((index + 1).toString(10));

    getImageClassName = (index: number) => {
        if(this.indexIsSelectedIndex(index))
        {
            return this.isRightAnswer(index) ? 'correct' : 'wrong';
        }
        else {
            return ''
        }
    }

    indexIsSelectedIndex = (index: number) => {
        return index === this.state.indexSelected
    }

    render() {
        const resourceUrls: string[] = this.props.question.resources !== undefined ? JSON.parse(this.props.question.resources) : [];
        return (
            <>
            <h6>Click the image that corresponds to the text</h6>
                <div className="multiple-choice-wrapper">
                   <hr/>
                    <Container>
                        <h4>{this.props.question.question}</h4>
                        <Row className='choices-container'>
                            {
                                resourceUrls.map( (resourceUrl, index) => {
                                    return (
                        
                                        <Col xs={6} md={4} lg={4}>
                                            <Image className={this.getImageClassName(index)+' '+'image-container'} 
                                                src={resourceUrl} 
                                                onClick={(event) => 
                                                    {
                                                    if (!this.isAnswerSelected())
                                                        this.handleImageClick(event, index);
                                                    }
                                                }/>
                                                {this.isAnswerSelected() && this.indexIsSelectedIndex(index) ? (this.isRightAnswer(index) ? <h4>Correct!</h4>: <h4>Wrong</h4>) : null}
                                        </Col>
                                       
                                        )
                                })
                                
                            }
                        </Row>
                    </Container>

                </div>
            </>
        )
    }
}

export default MultipleChoice;
