import {logError} from "../actions/analytics";
import {FunctionName, STACK} from "../models/analyticsModels";

const validator = require('validator');
const isEmpty = require('lodash/isEmpty');

export default function validateProfileInput(data: any) {
    let errors: any = {};

    // one of optional fields must be filled
    if(validator.isEmpty(data.newUsername) && validator.isEmpty(data.newEmail) && validator.isEmpty(data.newPassword)) {
        errors.newUsername = 'One of these fields must be filled';
        errors.email = 'One of these fields must be filled';
        errors.newPassword = 'One of these fields must be filled';

    } else {
        // required field regardless of which to update
        if (validator.isEmpty(data.password)) {
            errors.password = 'This field is required'
        } else if (data.password.length < 4 || data.password.length > 100) {
            errors.password = 'Invalid password';
        }

        // optional fields
        // if new username -> must be valid
        if(!validator.isEmpty(data.newUsername) && (data.newUsername.length < 4 || data.newUsername.length > 100)) {
            errors.newUsername = 'Invalid username';
        }

        // if new email -> must be valid
        if(!validator.isEmpty(data.newEmail) && (data.newEmail.length < 4 || data.newEmail.length > 100)) {
            errors.email = 'Invalid email';
        }

        // if new password:
        if(!validator.isEmpty(data.newPassword)) {
            // must be valid
            if(data.newPassword.length < 4 || data.newPassword.length > 100 ) {
                errors.newPassword = 'Invalid password';
            }

            // not equal current password
            if(data.password === data.newPassword){
                errors.newPassword = "New password can't equal the previous one";
            }

            // if new password, must confirm
            if(validator.isEmpty(data.confirmPassword)) {
                errors.newPassword = 'This field is required'
            } else if(data.confirmPassword.length < 4 || data.confirmPassword.length > 100 ) {
                errors.newPassword = 'Invalid password';
            }

            // new password and confirm password must match
            if(!validator.isEmpty(data.confirmPassword) && data.newPassword !== data.confirmPassword){
                errors.newPassword = "Passwords don't match";
            }
        }
    }

    if(!isEmpty(errors)) {
        logError(FunctionName.PROFILE_INPUT, errors, STACK.CLIENT)
    }

    return {
        errors,
        isValid: isEmpty(errors)
    }
}
