import React, { Component } from 'react';
import { connect} from 'react-redux';
import Header from '../../common/components/header/Header';
import Footer from '../../common/components/footer/Footer';
import girl from '../../assets/homepage-girl-computer-icon.svg';
import boy from '../../assets/homepage-boy-computer-icon.svg';
import bingo from '../../assets/homepage-bingo-icon.svg';
import {Link} from "react-router-dom";
import SignInButton from '../../common/components/user/buttons/SignUpButton';
import store from "../../state/store";
import {Route, Redirect} from "react-router-dom";
import Button from '@material-ui/core/Button';
import {loginAsGuest} from "../../state/actions/guest";
import './Homepage.css'
import {logPageNavigated, logUserAction} from "../../state/actions/analytics";
import {ButtonClicks, PageNavigation} from "../../state/models/analyticsModels";

export interface Props {}

interface State {
    username: string,
}

class Homepage extends Component<Props, State> {
    private unsubscribe: any;
    constructor(props: Props) {
        super(props);
        this.state = {
            username: ''
        }
        logPageNavigated(PageNavigation.HOMEPAGE)
    }

    componentWillMount() {
        this.unsubscribe = store.subscribe(() => {
            this.setState({
                username: store.getState().user.username
            });
        });
    }

    componentWillUnmount() {
        this.unsubscribe();
    }

    handleSighUpAsGuest = async (e: React.MouseEvent) => {
        e.preventDefault();
        logUserAction(ButtonClicks.GUEST);
        await loginAsGuest();
        this.setState({
            username: 'Guest'
        });
    }


    render() {
        return (
            <div>
                <Header />
                <Route exact path="/">
                    {this.state.username ? <Redirect to="/dashboard" /> : null}
                </Route>

                <div className='app-homepage'>
                    <div className='homepage-section-wrapper first-section'>
                        <div className='homepage-get-started-container'>
                            <span className='homepage-get-started-title'>
                                Learn American Sign Language
                            </span>
                            <span className='homepage-subtitle'>
                                Learn ASL for free from the comfort of your home at your convenience. No tutoring is needed. Our application will help you learn and evaluate your progress.
                            </span>
                            <div className='homepage-section-buttons-container'>
                                <div className='homepage-section-button left'>
                                    <SignInButton/>
                                </div>
                                <div className='homepage-section-button'>
                                    <Link id='guest' to="/dashboard">
                                        <Button variant="contained" onClick={this.handleSighUpAsGuest} >
                                            <span className='white-txt'>Continue as Guest</span>
                                        </Button>
                                    </Link>
                                </div>
                            </div>
                        </div>
                    </div>

                    <div className='homepage-section-divider'>
                        <span className='homepage-section-title'>Interaction ASL Lessons</span>
                    </div>

                    <div className='homepage-section-wrapper'>
                        <div className='homepage-section'>
                            <div className='section-half-cell left'>
                                <span className='homepage-subtitle vertical-align'>
                                    In addition to predefined lessons, we offer interactive lessons. With the help of your webcam, you will get the chance to practice what you learned.
                                </span>
                            </div>
                            <div className='homepage-icon-container section-half-cell right'>
                                <img src={girl} className='homepage-icon-container' alt='girl in computer'/>
                            </div>
                        </div>

                    </div>

                    <div className='homepage-section-divider'>
                        <span className='homepage-section-title'>Have Fun Learning</span>
                    </div>

                    <div className='homepage-section-wrapper'>
                        <div className='homepage-section'>
                            <div className='section-half-cell right'>
                                <span className='homepage-subtitle vertical-align'>
                                    We offer fun games designed for the hearing impaired to reinforce learning. These games will include bingo and flashcards.
                                </span>
                            </div>
                            <div className='homepage-icon-container section-half-cell left'>
                                <img src={bingo} className='homepage-icon-container' alt='plain sight logo bingo'/>
                            </div>
                        </div>
                    </div>

                    <div className='homepage-section-divider'>
                        <span className='homepage-section-title'>Resources</span>
                    </div>

                    <div className='homepage-section-wrapper'>
                        <div className='homepage-section'>
                            <div className='section-half-cell left'>
                                <span className='homepage-subtitle'>
                                    Our mission is to facilitate the ASL learning process for those who are hearing impaired. In keeping with our mission, we will provide helpful links with tips and other resources to help those who need guidance. In addition, our users will be able to communicate with other students and share knowledge and resources.
                                </span>
                            </div>
                            <div className='homepage-icon-container section-half-cell right'>
                                <img src={boy} className='homepage-icon-container' alt='boy in computer'/>
                            </div>
                        </div>
                    </div>

                    <div className='homepage-section-divider'></div>

                    <div className='homepage-section-wrapper'>
                        <div className='homepage-last-section'>
                            <div className='homepage-links-wrapper'>
                                <div className='homepage-links-section'>
                                    <Link id='about' to="/about">About us</Link>
                                    <div className='spacing-div-20'></div>
                                    <Link id='contact' to="/contact">Contact us</Link>
                                </div>
                                <div className='homepage-links-section'>
                                    <Link id='careers' to="/careers">Careers</Link>
                                    <div className='spacing-div-20'></div>
                                    <Link id='help' to="/help">Help and Support</Link>
                                </div>
                                <div className='homepage-links-section'>
                                    <Link id='terms' to="/terms">Terms</Link>
                                    <div className='spacing-div-20'></div>
                                    <Link id='privacy' to="/privacy">Privacy policy</Link>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}

const mapStateToProps = (state: any) => ({
    username: state.user.username
})

export default connect(mapStateToProps, {})(Homepage);
