import {GET_QUIZ_DATA, GET_QUIZZES, SESSION} from '../models/types';
import axios from "axios";
import { BASE_URL } from '../../environment'
import store from "../store";
import {logError, logServerRequestTime} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";


export async function getQuizzes(moduleId: string) {
    try{
        const username: any = await JSON.parse(localStorage.getItem(SESSION) || '{}').user.username;
        const sentTime = (new Date()).getTime();
        const res = await axios.get(BASE_URL + '/api/modules/' + moduleId + '/quizzes' + (username !== 'guest' ? `?username=${username}` : ''))
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.QUIZZES, receivedTime-sentTime);
        store.dispatch({
            type: GET_QUIZZES,
            payload: res.data?.quizzes
        })
        return res.data?.quizzes;
    }
    catch(e){
        logError(RequestName.QUIZZES, e, STACK.SERVER)
        return null;
    }
}

export async function getQuizData(quizId: string) {
    try{
        const sentTime = (new Date()).getTime();
        const res = await axios.get(BASE_URL + '/api/modules/quiz/' + quizId)
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.QUIZ_DATA, receivedTime-sentTime);
        store.dispatch({
            type: GET_QUIZ_DATA,
            payload: res.data?.questions
        })
        return res.data?.questions;
    }
    catch(e){
        logError(RequestName.QUIZ_DATA, e, STACK.SERVER)
        return null;
    }
}
