import React from "react";
import store from "../../../../../state/store";
import { QuizQuestion } from "../types/Quiz";
import "./FillTheBlanks.css";
import { Col, Container, Image, Row } from "react-bootstrap";
export interface TextInputQProps {
    question: QuizQuestion;
    onQuestionAnswered: (e: React.MouseEvent) => void;
}
export interface TextInputQState {
    username: string;
    answer: string;
    enterPressed: boolean
}
class FillTheBlanks extends React.Component<TextInputQProps, TextInputQState>{
    constructor(props: TextInputQProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            answer: '',
            enterPressed: false
        }
    }
    wasEnterPressed = () => this.state.enterPressed;
    handlePressedEnter = (e: React.MouseEvent) => {
        this.setState({enterPressed: true});
        this.props.onQuestionAnswered(e);
    }
    isRightAnswer = () => {
        return this.props.question.answer.toLowerCase() === this.state.answer.toLowerCase()
    }

    getClassNameImage = () => {
        return this.wasEnterPressed() ? (this.isRightAnswer() ? 'correct' : 'wrong') : ''
        
    }

    render() {
        return (
            <>
            <h6>Type your answer and press enter</h6>
            <hr></hr>
                <div className="text-input-wrapper">
                    <Container>
                        <Row>
                            <Col>
                                <Image 
                                className={this.getClassNameImage()}  
                                src={this.props.question.resources} />
                            </Col>
                        </Row>
                        
                        <Row>
                            <Col>
                            <input 
                                className={'inputField'} 
                                placeholder="Type you answer here and press enter"
                                onChange={(event) => {
                                    this.setState({ answer: event.target.value })
                                }}
                                onKeyPress={(event: any) =>{
                                    if (event.key === 'Enter') {
                                        this.handlePressedEnter(event);
                                      }
                                }}
                                disabled={this.wasEnterPressed()}
                            />
                            {this.wasEnterPressed() ? (this.isRightAnswer() ? <h3>Correct!</h3> : <h3>Wrong</h3>) : null}
                            </Col>
                        </Row>
                    </Container>
                </div>
            </>
        )
    }
}
export default FillTheBlanks;
