import React from "react";
import { Redirect, Route } from "react-router-dom";
import store from "../../state/store";

const PrivateRoute: any = ({ component: Component, ...rest }: any) => {
  let isAuth = store.getState().session.auth;

  return (
    <Route {...rest} render={props => (
        Object.entries(isAuth).length ?
            <Component {...props} />
        : <Redirect to="/" />
    )} />
  );
}

const AuthRoute: any = ({ component: Component, ...rest }: any) => {
  let isAuth = store.getState().session.auth;

  return (
    <Route {...rest} render={props => (
        !Object.entries(isAuth).length ?
            <Component {...props} />
        : <Redirect to="/dashboard" />
    )} />
  );
}

export { PrivateRoute, AuthRoute }
