import React from "react";
import { Row, Col } from "react-bootstrap";
import { connect } from "react-redux";
import "./Modules.css";
import {getModules} from "../../../../state/actions/modules";
import store from "../../../../state/store";
import { Paths } from "../../../../App";
import { Link, NavLink } from "react-router-dom";
import checkmark from '../../../../assets/checkmark.png';
import {logPageNavigated} from "../../../../state/actions/analytics";
import {PageNavigation} from "../../../../state/models/analyticsModels";

export interface ModulesProps {
    pathname?: string;
    modules: string[][];
    getModules: () => void;
}
export interface ModulesState {
    username: string;
}
class Modules extends React.Component<ModulesProps, ModulesState>{
    state = {
        username: store.getState().user.username
    }

    async componentDidMount() {
        await getModules();
        logPageNavigated(PageNavigation.MODULES)
    }

    getPath(moduleID: string) {
        if(this.props.pathname?.includes('quizzes')) {
            return Paths.dashboard + "/quizzes/" + moduleID;
        } else {
            return Paths.lessons + "/" + moduleID;
        }
    }

    getHeader(){
        if(this.props.pathname?.includes('quizzes')) {
            return (<h3 className='header-dashboard'>Quizzes</h3>)
        } else {
            return (<h3 className='header-dashboard'>Modules</h3>)
        }
    }

    render() {
        return (
            <>
            {this.getHeader()}
                <div className="module-buttons-wrapper">
                    {this.props.modules !== undefined && this.props.modules !== null ?
                        this.props.modules.map(
                            (moduleAsArray: string[]) => {
                                const moduleName = moduleAsArray[1];
                                const moduleID = moduleAsArray[0];
                                const moduleCompletion = moduleAsArray[3];
                                let checkmarkImage;
                                if (parseInt(moduleCompletion) === 100){
                                    checkmarkImage = <img src={checkmark} className='checkmark-logo' alt='in plain sight logo'/>;
                                }
                                return <Link key={moduleID+moduleName} to={
                                    {
                                        pathname:this.getPath(moduleID),
                                        state: {moduleName: moduleName}
                                    }}>
                                    <Row className={"module-row"}>
                                        <Col>
                                            <p className={"module-title"}>{moduleName}</p>
                                        </Col>
                                        <Col>
                                            {checkmarkImage}
                                        </Col>
                                    </Row>
                                </Link>

                            }
                        )
                        : null}
                </div>
            </>
        )
    }
}

const mapStateToProps = (state: any) => ({
    modules: state.module.modules
})
const mapDispatchToProps = {
    getModules: getModules
}
export default connect(mapStateToProps, mapDispatchToProps)(Modules);
