import React from "react"
import {Card, CardColumns} from "react-bootstrap";
import CardContainer from "../../../../common/components/cards/Cards";
import store from "../../../../state/store";
import {getModuleLessons, getModules, setCurrentLesson} from "../../../../state/actions/modules";
import { Paths } from "../../../../App";
import { NavLink } from "react-router-dom";
import "./Lesson.css";
import {logPageNavigated} from "../../../../state/actions/analytics";
import {PageNavigation} from "../../../../state/models/analyticsModels";
import { connect } from "react-redux";


interface LessonProps {
    modules: any[][];
    getModules: () => void;
    pathname?: string;
    match?: { params?: { moduleId?: string } },
    moduleId?: string;
}

interface LessonState {
    username: string;
    lessons: any[];
    moduleId: string;
}

class Lesson extends React.Component<LessonProps, LessonState> {
    constructor(props: LessonProps) {
        super(props);
        this.state = {
            username: store.getState().user.username,
            lessons: [],
            moduleId: this.props?.moduleId || ''
        }
        logPageNavigated(PageNavigation.LESSONS);
    }

    async componentDidMount() {
        await getModuleLessons(this.state.moduleId);

        const lessons = store.getState().module.lessons;
        if(lessons.length !== 0) {
            this.setState({lessons});
        }

        await getModules();
    }

    linkToLesson = (lessonName:string) => {
        setCurrentLesson(lessonName);
        return null;
    };
    checkIfProgressNull = (progress:number) => {
        if (progress){
            return progress;
        }
        return 0; 
    }

    getModuleName(){
        const module = this.props.modules.filter((moduleAsArray: any[]) => {
            const moduleID = moduleAsArray[0];
            if(this.props.moduleId){
                return moduleID === parseInt(this.props.moduleId, 10)
            }           
        })[0]

        if(module)
        {
            return module[1]
        }
    }

    render() {
        return (
            <>
            <h3 className='header-dashboard'>{this.getModuleName()}</h3>
            <div className="cards-container">
            <CardColumns>
                {this.state.lessons.length !== 0 ?
                    this.state.lessons.map(
                        (lesson: any) => {
                           return <Card 
                           style = {{width: '70%', marginTop: '10%'}}
                           onClick={() => this.linkToLesson(lesson.name)}  
                           key={this.state.moduleId}>
                            <NavLink 
                            key={this.state.moduleId+lesson.name} 
                            to={Paths.lessons + "/" + this.state.moduleId + "/" + lesson.lesson_id}
                            className='no-margin'>
                                <CardContainer
                                    name={lesson.name}
                                    description={lesson.description}
                                    progress={this.checkIfProgressNull(lesson.progress)}
                                    imageUrl={lesson.picture}
                                    />
                            </NavLink>
                            </Card>

                        }
                    )
                    : null}  
                </CardColumns>
                </div>
            </>
        )
    }
}

const mapStateToProps = (state: any) => ({
    modules: state.module.modules
})
const mapDispatchToProps = {
    getModules: getModules
}

export default connect(mapStateToProps, mapDispatchToProps)(Lesson);
