import {LOGOUT, SESSION} from '../models/types';
import store from "../store";
import {logError} from "./analytics";
import {FunctionName, STACK} from "../models/analyticsModels";

/* Set of functions to save, delete, update, and retrieve information
from the browser local storage */

export async function setSession(key: string, data: any) {
  try {
    localStorage.setItem(key, JSON.stringify(data));

    store.dispatch({
      type: SESSION,
      payload: data
    });

    return true;

  } catch(err) {
    logError(FunctionName.SET_SESSION, err, STACK.CLIENT);
    return false;
  }
}

export async function updateSession(key: string, data: any) {
  try {
    const token = JSON.parse(localStorage.getItem(SESSION) as string)?.token;
    const newItem = {
      token,
      user: data
    };
    localStorage.setItem(key, JSON.stringify(newItem));

    store.dispatch({
      type: SESSION,
      payload: data
    });

    return true;

  } catch(err) {
    logError(FunctionName.UPDATE_SESSION, err, STACK.CLIENT);
    return false;
  }
}

export function clearSession() {
  try {
    localStorage.clear();

    store.dispatch({
      type: LOGOUT,
      payload: false
    })

  } catch(err) {
    logError(FunctionName.CLEAR_SESSION, err, STACK.CLIENT);
  }
}
