import React from 'react';
import Button from 'react-bootstrap/Button';
import 'bootstrap/dist/css/bootstrap.min.css';

interface SignUpOptionsProps {
    id?:string;
    onSignUpClicked: (e: React.MouseEvent) => void;
    onLoginClicked: (e: React.MouseEvent) => void;
    onContinueWithGoogleClicked: (e: React.MouseEvent) => void;
}
class SignUpOptions extends React.Component<SignUpOptionsProps>{
    render() {
        return (
            <div id={this.props.id}>
                <Button
                    id="modalSignUpButton"
                    variant="primary"
                    data-cy='submit-registration'
                    type="submit"
                    onClick={this.props.onSignUpClicked}
                >
                    Sign Up
                </Button>

                <p className="horizontalLine"><span>or</span></p>

                <Button
                    id="modalContinueWithGoogleButton"
                    variant="primary"
                    type="submit"
                    onClick={this.props.onContinueWithGoogleClicked}
                >
                    Continue with Google
                </Button>

                <p>Already have an account?</p>

                <Button
                    id="modalLoginButton"
                    variant="primary"
                    type="submit"
                    onClick={this.props.onLoginClicked}
                >
                    Login
                </Button>
            </div>
        )
    }
}
export default SignUpOptions;
