import React, { useState } from "react";
import Button from '@material-ui/core/Button';
import UserManagement from './../UserManagement';
import {TypeOfUserManagement} from "../../../../state/models/userManagementModel";

function SignUpButton(){
    const [signUpModalVisible, setSignUpModalVisible] = useState<boolean>(false);

    return (<> 
    <UserManagement
        visible={signUpModalVisible}
        onHide={() => { setSignUpModalVisible(false) }}
        type={TypeOfUserManagement.SIGN_UP}
    />

    <Button
        type="submit"
        data-cy='register'
        onClick={() => { setSignUpModalVisible(true) }}
    >
        <span className='white-txt'>Get started</span>
    </Button>

     </>)
}
export default SignUpButton;
