import axios from "axios";
import {BASE_URL} from "../../environment";
import {GET_RESOURCES} from "../models/types";
import store from "../store";
import {RequestName, STACK} from "../models/analyticsModels";
import {logError, logServerRequestTime} from "./analytics";


export async function getResources() {

    try{
        const sentTime = (new Date()).getTime();
        let response = await axios.get(BASE_URL + '/resources')
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.RESOURCES, receivedTime-sentTime);

        store.dispatch({
            type: GET_RESOURCES,
            payload: response.data.resources
        });
        return true;
    }
    catch(e){
        logError(RequestName.RESOURCES, e.response.data, STACK.SERVER)
        console.error(e.response.data);
        return false;
    }
}
