import {MIXPANEL_TOKEN} from "../../environment";
import {ButtonClicks, EventTypes, FunctionName, PageNavigation, RequestName, STACK} from "../models/analyticsModels";
import store from "../store";
const mixpanel = require('mixpanel-browser');

export const setUser = function(user :any){
    try{
        // Start mixpanel initiation with proper id
        mixpanel.init(MIXPANEL_TOKEN);

        // set user properties
        mixpanel.people.set({
            $distinct_id: user.email,
            $name: user.username,
            $email: user.email
        });

        // identify must be called along with every instance of people.set
        mixpanel.identify(user.email);
    } catch(e) {
        console.error(e);
    }

}

export const logPageNavigated = function(page: PageNavigation) {
    try{
        const user = store.getState().session?.auth?.user || 'guest@user.com';

        mixpanel.init(MIXPANEL_TOKEN);
        mixpanel.track(EventTypes.NAVIGATION + page, {
            distinct_id: user.email,
            $name: user.username,
            $email: user.email,
        });
    } catch(e) {
        console.error(e);
    }
}

export const logError = function(requestName: RequestName | FunctionName, errorString: string, stack: STACK) {
    try{
        const user = store.getState().session?.auth?.user || 'guest@user.com';
        const errorDescription = errorString;

        mixpanel.init(MIXPANEL_TOKEN);
        mixpanel.track(EventTypes.ERROR, {
            distinct_id: user.email,
            "Request Name": requestName,
            "Description": errorDescription,
            "Stack": stack,
        });
    } catch(e) {
        console.error(e);
    }
}

