import {GUEST, SESSION} from '../models/types';
import store from "../store";

import { setSession } from './session';
import {logError, logServerRequestTime} from "./analytics";
import {RequestName, STACK} from "../models/analyticsModels";

const randomToken = require('random-token');

export async function loginAsGuest() {
    try{
        const sentTime = (new Date()).getTime();
        let data = {
          token: randomToken(40),
          user: {
            username: "guest",
            email: "guest"
          }
        }
        const receivedTime = (new Date()).getTime();
        logServerRequestTime(RequestName.GUEST, receivedTime-sentTime);

        setSession(SESSION, data);

        store.dispatch({
            type: GUEST,
            payload: '* Guest *'
        });
    }
    catch(e){
        logError(RequestName.GUEST, e, STACK.SERVER)
        console.error(e);
    }
}
